*{
    margin: 0; 
    padding: 0;
    box-sizing: border-box;
  }
 
html, body {
    width: 100%;
    height: 100%;
}

body{
    background-color: silver;
    font-family: 'Source Sans Pro', sans-serif;
}

header, footer{
    width: 100%;  
}

header{
    height: 15%;  
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    text-align: center;
}

#emblem{
    width: 254px;
    height: 153px;
    background-image: url('../img/010101.png');
    background-repeat: no-repeat;
    display: none;    
}

#languageBtn{
    width: 64px;
    height: 33px;
    background-image: url('../img/uk64.png');
}

#mainContainer{
    width: 90%;
    height: 75%;
    margin: 0 auto;
}

footer{
    width: 100%;
    height: 10%;
    text-align: center;
}


@media screen and (min-width: 768px){
    header{
        height: 15%;  
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-content: center;
        align-items: center;
    }
}

@media screen and (min-width: 1024px) {
    #emblem{
        display: block;    
    }

    #languageBtn{
        width: 128px;
        height: 65px;
        background-image: url('../img/uk128.png');
    }
}